.Github-card{
    align-items: center;
    background-color: transparent;
    border: 1px solid #9a9b9c;
    color: black;
    width: 100%;
    height: 100%;
    -webkit-box-shadow: 0px 18px 18px -7px rgba(0,0,0,0.36);
-moz-box-shadow: 0px 18px 18px -7px rgba(0,0,0,0.36);
box-shadow: 0px 18px 18px -7px rgba(0,0,0,0.36);
border-radius: 10px;
}

.profile-image{
    border-radius: 50%;
    height: 150px;
    margin-top: 20px;
    margin-left: 40px;
    width: auto;
    border: 1px solid #333333;
    -webkit-box-shadow: 0px 18px 18px -7px rgba(0,0,0,0.36);
    -moz-box-shadow: 0px 18px 18px -7px rgba(0,0,0,0.36);
    box-shadow: 0px 18px 18px -7px rgba(0,0,0,0.36);
}

.profile-name{
    font-size: 20px;
    font-weight: bold;
    color: white !important;
    white-space: nowrap;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
}

.profile-header{
    margin-top: 50px;
}

.profile-follow{
    text-align: center;
    margin-top: 30px;
    padding-left: 40px;
    padding-right: 40px;
    color: #9a9b9c;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
}

.profile-box{
    background-color: transparent;
    border: 1px solid #9a9b9c;
    border-radius: 10px;
    margin-top: 15px;
    padding-top: 10px;
}

.profile-link{
    color: #9a9b9c !important;
}

.Card {
    text-align: left !important;
    margin-top: 25px;
    height: 500px;
}

.number-same{
    color: orange;
    font-size: 20px;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
}

.number-greater{
    color: green;
    font-size: 20px;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
}

.number-lower{
    color: red;
    font-size: 20px;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
}

.error-text{
    margin-top: 10px;
    text-align: center;
    color: #9a9b9c;
}


::placeholder{
    color:  #9a9b9c !important;
}

.input-fields {
    background-color: #282c34 !important; 
    color: white !important;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
    border: 1px solid  #9a9b9c !important;
}

.header-title {
    color: #9a9b9c;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
    padding-left: 15px;
    padding-top: 10px;
}

.label{
    color: #9a9b9c;
    font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
}

@media screen and (min-width: 0px) and (max-width: 768px) {
    .header-title {
        color: #9a9b9c;
        font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
        padding-left: 15px;
        padding-top: 10px;
        text-align: center;
        margin-bottom: 50px;
    }

    .Card {
        text-align: center !important;
        margin-top: 25px;
        height: 620px;
    }

    .profile-image{
        border-radius: 50%;
        height: 150px;
        margin-top: 20px;
        margin-left: 0px;
        width: auto;
    }

    .profile-header{
        width: 100%;
        margin-top: 50px;
    }

    .profile-follow{
        text-align: center;
        margin-top: 10px;
        padding-left: 40px;
        padding-right: 40px;
        color: #9a9b9c;
        font-family: Consolas, "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
    }

}